.work-container {
  z-index: 50;
  background-color: rgb(220, 220, 220);
  display: block;
  position: relative;
  .section-box {
    display: flex;
    flex-direction: column;
    padding: 50px 50px 25px 50px;
  }
}
.card-wrapper{
  height: 650px;
  display: flex;
}

.card-container {
  width: 240px;
  display: flex;
  flex-direction: column;

  img{
    border-radius: 12%;
    display: flex;
    width: 180px;
    transition: all 300ms;
    // filter: saturate(0.9);
    filter: drop-shadow(10px 16px 18px rgba(255, 187, 0, 0.582)) saturate(0.5);
    &:hover {
      width: 190px;
    }
  }
}

.text-container {
  flex-direction: column;
  display: flex;
  p:nth-of-type(1) {
    font-weight: 400;
    margin: 1rem;
    text-align: justify;
  }
  p:nth-of-type(2) {
    font-size: 1rem;
    margin-bottom: 1rem;
    margin-top: 1rem;
  }
}
